// Configuration Firebase pour l'application Collectif le Feydeau
import { initializeApp } from 'firebase/app';
import { getAnalytics } from 'firebase/analytics';
import { getFirebaseConfig } from '@/config/firebase-prod';

// Obtenir la configuration Firebase (env ou production)
const firebaseConfig = getFirebaseConfig();

// Variable pour stocker l'instance Firebase
let firebaseApp: any = null;

// Fonction pour initialiser Firebase de manire conditionnelle
const initializeFirebase = () => {
  // Vrifier si la cl API est disponible
  if (firebaseConfig.apiKey) {
    try {
      console.log(' [Firebase] Initialisation avec cl API:', firebaseConfig.apiKey.substring(0, 10) + '...');
      return initializeApp(firebaseConfig);
    } catch (error) {
      console.warn('Erreur lors de l\'initialisation de Firebase:', error);
      return null;
    }
  } else {
    console.warn('Firebase non initialis: cl API manquante');
    return null;
  }
};

// Initialiser Firebase uniquement si la cl API est disponible
export const getFirebaseApp = () => {
  if (!firebaseApp) {
    firebaseApp = initializeFirebase();
  }
  return firebaseApp;
};

// Initialiser Analytics (uniquement ct client et si Firebase est initialis)
let analyticsInstance: any = null;

export const initFirebaseAnalytics = () => {
  if (typeof window !== 'undefined') {
    const app = getFirebaseApp();
    if (app) {
      try {
        analyticsInstance = getAnalytics(app);
        return analyticsInstance;
      } catch (error) {
        console.warn('Erreur lors de l\'initialisation de Firebase Analytics:', error);
      }
    }
  }
  return null;
};

export const getFirebaseAnalytics = () => analyticsInstance;

