/**
 * Gestionnaire de cache pour stocker les donnes frquemment utilises
 * Cela permet d'amliorer les performances et de rduire les recherches rptitives
 */

type CacheItem<T> = {
  data: T;
  timestamp: number;
  expiry: number | null; // null = pas d'expiration
};

class CacheManager {
  private cache: Map<string, CacheItem<any>> = new Map();
  private defaultExpiry: number = 30 * 60 * 1000; // 30 minutes par dfaut

  /**
   * Dfinit une valeur dans le cache
   * @param key - Cl unique pour identifier les donnes
   * @param data - Donnes  mettre en cache
   * @param expiry - Dure de validit en millisecondes (null = pas d'expiration)
   */
  set<T>(key: string, data: T, expiry: number | null = this.defaultExpiry): void {
    this.cache.set(key, {
      data,
      timestamp: Date.now(),
      expiry
    });
  }

  /**
   * Rcupre des donnes du cache
   * @param key - Cl des donnes  rcuprer
   * @returns Les donnes si elles existent et sont valides, sinon null
   */
  get<T>(key: string): T | null {
    const item = this.cache.get(key);
    
    if (!item) return null;
    
    // Vrifier si les donnes ont expir
    if (item.expiry !== null) {
      const now = Date.now();
      if (now - item.timestamp > item.expiry) {
        this.cache.delete(key);
        return null;
      }
    }
    
    return item.data as T;
  }

  /**
   * Rcupre des donnes du cache ou les gnre si elles n'existent pas
   * @param key - Cl des donnes
   * @param generator - Fonction qui gnre les donnes si elles ne sont pas en cache
   * @param expiry - Dure de validit en millisecondes
   * @returns Les donnes du cache ou nouvellement gnres
   */
  async getOrSet<T>(
    key: string, 
    generator: () => Promise<T>, 
    expiry: number | null = this.defaultExpiry
  ): Promise<T> {
    const cachedData = this.get<T>(key);
    
    if (cachedData !== null) {
      return cachedData;
    }
    
    const freshData = await generator();
    this.set(key, freshData, expiry);
    return freshData;
  }

  /**
   * Supprime des donnes du cache
   * @param key - Cl des donnes  supprimer
   */
  remove(key: string): void {
    this.cache.delete(key);
  }

  /**
   * Vrifie si une cl existe dans le cache et est valide
   * @param key - Cl  vrifier
   * @returns true si la cl existe et est valide, sinon false
   */
  has(key: string): boolean {
    return this.get(key) !== null;
  }

  /**
   * Vide le cache entirement
   */
  clear(): void {
    this.cache.clear();
  }

  /**
   * Nettoie les entres expires du cache
   */
  cleanup(): void {
    const now = Date.now();
    
    for (const [key, item] of this.cache.entries()) {
      if (item.expiry !== null && now - item.timestamp > item.expiry) {
        this.cache.delete(key);
      }
    }
  }
}

// Exporter une instance unique du gestionnaire de cache
export const cacheManager = new CacheManager();

