/**
 * Utilitaire de diagnostic pour Firebase Analytics
 * Permet de vrifier si la configuration est correcte et si les vnements peuvent tre envoys
 */

import { getFirebaseAnalytics } from '../services/firebaseConfig';
import { getFirebaseApp } from '../services/firebaseConfig';
import { getAnalytics, isSupported, setAnalyticsCollectionEnabled } from 'firebase/analytics';

interface DiagnosticResult {
  success: boolean;
  message: string;
  details?: any;
}

/**
 * Vrifie si Firebase Analytics est correctement configur
 */
export const runAnalyticsDiagnostics = async (): Promise<DiagnosticResult[]> => {
  const results: DiagnosticResult[] = [];
  
  try {
    // 1. Vrifier si Analytics est support dans cet environnement
    const supported = await isSupported();
    results.push({
      success: supported,
      message: supported 
        ? "Firebase Analytics est support dans cet environnement" 
        : "Firebase Analytics n'est PAS support dans cet environnement"
    });
    
    if (!supported) {
      return results;
    }
    
    // 2. Vrifier si Firebase est initialis
    const app = getFirebaseApp();
    const isFirebaseInitialized = !!app;
    results.push({
      success: isFirebaseInitialized,
      message: isFirebaseInitialized 
        ? "Firebase est correctement initialis" 
        : "Firebase n'est PAS initialis"
    });
    
    if (!isFirebaseInitialized) {
      return results;
    }
    
    // 3. Vrifier la configuration Firebase
    const config = app ? app.options : {};
    const hasMeasurementId = !!config.measurementId;
    results.push({
      success: hasMeasurementId,
      message: hasMeasurementId 
        ? "MeasurementId est configur" 
        : "MeasurementId est MANQUANT dans la configuration",
      details: {
        measurementId: config.measurementId || "Non dfini",
        apiKey: config.apiKey ? "Configur" : "Non configur",
        projectId: config.projectId || "Non dfini"
      }
    });
    
    // 4. Vrifier si Analytics peut tre initialis
    let analytics = null;
    try {
      if (app) {
        analytics = getAnalytics(app);
      } else {
        console.warn('Firebase non initialis, impossible d\'utiliser Analytics');
      }
      results.push({
        success: !!analytics,
        message: !!analytics 
          ? "Analytics a t initialis avec succs" 
          : "chec de l'initialisation d'Analytics"
      });
    } catch (error) {
      results.push({
        success: false,
        message: "Erreur lors de l'initialisation d'Analytics",
        details: { error: error instanceof Error ? error.message : String(error) }
      });
      return results;
    }
    
    // 5. Vrifier les paramtres de debug dans l'URL
    const url = new URL(window.location.href);
    const hasFirebaseDebug = url.searchParams.has('firebase_debug');
    const hasDebugMode = url.searchParams.has('debug_mode');
    const hasGtmDebug = url.searchParams.has('gtm_debug');
    
    results.push({
      success: hasFirebaseDebug || hasDebugMode || hasGtmDebug,
      message: (hasFirebaseDebug || hasDebugMode || hasGtmDebug)
        ? "Paramtres de debug dtects dans l'URL" 
        : "Aucun paramtre de debug dans l'URL",
      details: {
        firebase_debug: hasFirebaseDebug ? "Prsent" : "Absent",
        debug_mode: hasDebugMode ? "Prsent" : "Absent",
        gtm_debug: hasGtmDebug ? "Prsent" : "Absent"
      }
    });
    
    // 6. Vrifier si la collecte de donnes est active
    try {
      // Activer la collecte pour le test
      setAnalyticsCollectionEnabled(analytics, true);
      results.push({
        success: true,
        message: "La collecte de donnes Analytics est active"
      });
    } catch (error) {
      results.push({
        success: false,
        message: "Erreur lors de l'activation de la collecte de donnes",
        details: { error: error instanceof Error ? error.message : String(error) }
      });
    }
    
    // 7. Vrifier si window.gtag est disponible (pour le mode debug)
    const hasGtag = typeof window.gtag === 'function';
    results.push({
      success: hasGtag,
      message: hasGtag 
        ? "La fonction gtag est disponible" 
        : "La fonction gtag n'est PAS disponible"
    });
    
    // 8. Vrifier les bloqueurs potentiels
    const potentialBlockers = checkForBlockers();
    results.push({
      success: potentialBlockers.length === 0,
      message: potentialBlockers.length === 0
        ? "Aucun bloqueur potentiel dtect"
        : `${potentialBlockers.length} bloqueur(s) potentiel(s) dtect(s)`,
      details: { blockers: potentialBlockers }
    });
    
  } catch (error) {
    results.push({
      success: false,
      message: "Erreur lors du diagnostic",
      details: { error: error instanceof Error ? error.message : String(error) }
    });
  }
  
  return results;
};

/**
 * Vrifie la prsence potentielle de bloqueurs
 */
function checkForBlockers(): string[] {
  const blockers: string[] = [];
  
  // Vrifier si des extensions de blocage courantes sont dtectes
  if (document.getElementById('adblock-detector') || 
      document.getElementById('ublock-detector') ||
      document.getElementById('adblock-notice')) {
    blockers.push("Bloqueur de publicits dtect");
  }
  
  // Vrifier si les cookies sont bloqus
  try {
    document.cookie = "testcookie=1; SameSite=Lax; Secure";
    if (document.cookie.indexOf("testcookie=") === -1) {
      blockers.push("Les cookies sont bloqus");
    }
  } catch (e) {
    blockers.push("Erreur lors du test des cookies");
  }
  
  // Vrifier si localStorage est disponible
  try {
    localStorage.setItem('test', 'test');
    localStorage.removeItem('test');
  } catch (e) {
    blockers.push("localStorage n'est pas disponible");
  }
  
  // Vrifier si le navigateur est en mode priv/incognito (peut affecter certains trackers)
  // Note: Ces API sont spcifiques  certains navigateurs et ncessitent des types personnaliss
  interface FileSystem {
    root: any;
  }
  
  interface FileError {
    code: number;
  }
  
  type RequestFileSystemCallback = (fs: FileSystem) => void;
  type ErrorCallback = (err: FileError) => void;
  
  interface RequestFileSystemFunction {
    (type: number, size: number, successCallback: RequestFileSystemCallback, errorCallback?: ErrorCallback): void;
  }
  
  // Dfinir les constantes manquantes
  const TEMPORARY = 0;
  
  // Utiliser des cast de type pour accder aux API non standard
  const requestFS = ((window as any).RequestFileSystem || (window as any).webkitRequestFileSystem) as RequestFileSystemFunction | undefined;
  
  if (requestFS) {
    try {
      requestFS(TEMPORARY, 100, 
        () => {}, // Rien  faire si ce n'est pas en mode priv
        () => { blockers.push("Navigation prive dtecte"); }
      );
    } catch (e) {
      // Ignorer les erreurs, cette vrification est optionnelle
    }
  }
  
  return blockers;
}

/**
 * Excute le diagnostic et affiche les rsultats dans la console
 */
export const runAndLogDiagnostics = async () => {
  console.group(' Diagnostic Firebase Analytics');
  console.log('Dmarrage du diagnostic...');
  
  const results = await runAnalyticsDiagnostics();
  
  let successCount = 0;
  let failureCount = 0;
  
  results.forEach((result, index) => {
    if (result.success) {
      successCount++;
      console.log(` ${index + 1}. ${result.message}`);
    } else {
      failureCount++;
      console.warn(` ${index + 1}. ${result.message}`);
    }
    
    if (result.details) {
      console.log('  Dtails:', result.details);
    }
  });
  
  console.log(`\nRsultat: ${successCount} succs, ${failureCount} problmes`);
  
  if (failureCount > 0) {
    console.log('\nRecommandations:');
    console.log('1. Vrifiez que la configuration Firebase est correcte');
    console.log('2. Assurez-vous que les paramtres de debug sont prsents dans l\'URL');
    console.log('3. Dsactivez les bloqueurs de publicits');
    console.log('4. Utilisez Chrome pour les tests');
    console.log('5. Vrifiez que le measurementId correspond  votre projet Google Analytics');
  }
  
  console.groupEnd();
  
  return {
    results,
    summary: {
      total: results.length,
      success: successCount,
      failure: failureCount
    }
  };
};

