import { cacheManager } from '@/utils/cacheManager';
import { getSavedEvents } from './savedEvents';
import { Event, getEventById } from '@/data/events';
import { Location, getLocationNameById, getLocations } from '@/data/locations';
import { getArtistById } from '@/data/artists';

// Fonction pour obtenir la base URL de l'application
const getBaseUrl = (): string => {
  if (typeof window !== 'undefined' && window.APP_CONFIG && window.APP_CONFIG.BASE_URL) {
    return window.APP_CONFIG.BASE_URL;
  }
  return '/';
};

// Fonction pour construire un chemin complet avec la base URL
const getFullPath = (path: string): string => {
  const baseUrl = getBaseUrl();
  // Si le chemin commence dj par la base URL, le retourner tel quel
  if (path.startsWith(baseUrl)) {
    return path;
  }
  // Si le chemin commence par un slash, le supprimer pour viter les doubles slashs
  const cleanPath = path.startsWith('/') ? path.substring(1) : path;
  return `${baseUrl}${cleanPath}`;
};

// Constantes pour les cls de cache
const CACHE_KEYS = {
  SAVED_EVENTS: 'offline_saved_events',
  LOCATIONS: 'offline_locations',
  MAP_IMAGE: 'offline_map_image',
  LOCATION_IMAGES: 'offline_location_images',
  HISTORY_IMAGES: 'offline_history_images',
};

// Dure de validit du cache en millisecondes (7 jours)
const CACHE_EXPIRY = 7 * 24 * 60 * 60 * 1000;

/**
 * Prcharge les dtails des vnements sauvegards pour une utilisation hors-ligne
 * @returns Promise<Event[]> Les vnements sauvegards prchargs
 */
export const preloadSavedEvents = async (): Promise<Event[]> => {
  try {
    console.log('[OfflineService] Prchargement des vnements sauvegards');
    
    // Rcuprer les vnements sauvegards
    const savedEvents = getSavedEvents();
    
    if (savedEvents.length === 0) {
      console.log('[OfflineService] Aucun vnement sauvegard  prcharger');
      return [];
    }
    
    // Stocker les vnements dans le cache
    cacheManager.set(CACHE_KEYS.SAVED_EVENTS, savedEvents, CACHE_EXPIRY);
    console.log(`[OfflineService] ${savedEvents.length} vnements sauvegards mis en cache`);
    
    return savedEvents;
  } catch (error) {
    console.error('[OfflineService] Erreur lors du prchargement des vnements:', error);
    return [];
  }
};

/**
 * Rcupre les vnements sauvegards depuis le cache ou les prcharge si ncessaire
 * @returns Promise<Event[]> Les vnements sauvegards
 */
export const getOfflineSavedEvents = async (): Promise<Event[]> => {
  return cacheManager.getOrSet(
    CACHE_KEYS.SAVED_EVENTS,
    async () => preloadSavedEvents(),
    CACHE_EXPIRY
  );
};

/**
 * Prcharge les lieux associs aux vnements sauvegards
 * @returns Promise<Location[]> Les lieux prchargs
 */
export const preloadLocations = async (): Promise<Location[]> => {
  try {
    console.log('[OfflineService] Prchargement des lieux');
    
    // Rcuprer les vnements sauvegards
    const savedEvents = await getOfflineSavedEvents();
    
    if (savedEvents.length === 0) {
      console.log('[OfflineService] Aucun lieu  prcharger (pas d\'vnements sauvegards)');
      return [];
    }
    
    // Rcuprer tous les lieux
    const allLocations = getLocations();
    
    // Filtrer les lieux associs aux vnements sauvegards
    const locationIds = savedEvents.map(event => event.locationId);
    const uniqueLocationIds = [...new Set(locationIds)];
    const relevantLocations = allLocations.filter(location => 
      uniqueLocationIds.includes(location.id)
    );
    
    // Stocker les lieux dans le cache
    cacheManager.set(CACHE_KEYS.LOCATIONS, relevantLocations, CACHE_EXPIRY);
    console.log(`[OfflineService] ${relevantLocations.length} lieux mis en cache`);
    
    return relevantLocations;
  } catch (error) {
    console.error('[OfflineService] Erreur lors du prchargement des lieux:', error);
    return [];
  }
};

/**
 * Rcupre les lieux depuis le cache ou les prcharge si ncessaire
 * @returns Promise<Location[]> Les lieux
 */
export const getOfflineLocations = async (): Promise<Location[]> => {
  return cacheManager.getOrSet(
    CACHE_KEYS.LOCATIONS,
    async () => preloadLocations(),
    CACHE_EXPIRY
  );
};

/**
 * Prcharge les images des lieux pour une utilisation hors-ligne
 * @returns Promise<string[]> Les URLs des images prcharges
 */
export const preloadLocationImages = async (): Promise<string[]> => {
  try {
    console.log('[OfflineService] Prchargement des images des lieux');
    
    // Rcuprer les lieux
    const locations = await getOfflineLocations();
    
    if (locations.length === 0) {
      console.log('[OfflineService] Aucune image de lieu  prcharger');
      return [];
    }
    
    // Rcuprer les URLs des images
    const imageUrls = locations
      .filter(location => location.image)
      .map(location => {
        const imagePath = location.image as string;
        // Si l'URL est dj absolue (commence par http ou https), la laisser telle quelle
        if (imagePath.startsWith('http://') || imagePath.startsWith('https://')) {
          return imagePath;
        }
        // Sinon, ajouter la base URL
        return getFullPath(imagePath);
      });
    
    // Prcharger chaque image
    const preloadPromises = imageUrls.map(async (url) => {
      try {
        // Utiliser fetch pour prcharger l'image et la mettre en cache
        const response = await fetch(url, { 
          cache: 'force-cache',
          headers: {
            'Cache-Control': 'max-age=31536000', // 1 an
          },
          mode: 'no-cors' // Permet le chargement mme en cas de problmes CORS
        });
        
        if (!response.ok && response.type !== 'opaque') {
          throw new Error(`Erreur lors du chargement de l'image: ${response.status}`);
        }
        
        // Crer un blob  partir de la rponse pour garantir que l'image est compltement tlcharge
        const blob = await response.blob();
        
        // Stocker les mtadonnes de l'image dans le cache
        cacheManager.set(`${CACHE_KEYS.LOCATION_IMAGES}_${url}`, {
          url,
          size: blob.size,
          type: blob.type,
          timestamp: Date.now()
        }, CACHE_EXPIRY);
        
        console.log(`[OfflineService] Image prcharge: ${url} (${blob.size} octets)`);
        
        // galement mettre en cache dans le cache du navigateur via le service worker
        if ('serviceWorker' in navigator && navigator.serviceWorker.controller) {
          navigator.serviceWorker.controller.postMessage({
            type: 'CACHE_LOCATION_IMAGE',
            url
          });
        }
        
        return url;
      } catch (error) {
        console.error(`[OfflineService] Erreur lors du prchargement de l'image ${url}:`, error);
        return null;
      }
    });
    
    const results = await Promise.all(preloadPromises);
    const successfulUrls = results.filter(url => url !== null) as string[];
    
    console.log(`[OfflineService] ${successfulUrls.length}/${imageUrls.length} images prcharges`);
    
    return successfulUrls;
  } catch (error) {
    console.error('[OfflineService] Erreur lors du prchargement des images:', error);
    return [];
  }
};

/**
 * Prcharge les images des historiques des lieux pour une utilisation hors-ligne
 * @returns Promise<string[]> Les URLs des images prcharges
 */
export const preloadHistoryImages = async (): Promise<string[]> => {
  try {
    console.log('[OfflineService] Prchargement des images des historiques');
    
    // Rcuprer tous les lieux (pas seulement ceux lis aux vnements sauvegards)
    const allLocations = getLocations();
    
    // Filtrer les lieux qui ont un historique et une image
    const locationsWithHistory = allLocations.filter(location => 
      location.history && location.image
    );
    
    if (locationsWithHistory.length === 0) {
      console.log('[OfflineService] Aucune image d\'historique  prcharger');
      return [];
    }
    
    // Rcuprer les URLs des images
    const imageUrls = locationsWithHistory.map(location => {
      const imagePath = location.image as string;
      // Si l'URL est dj absolue (commence par http ou https), la laisser telle quelle
      if (imagePath.startsWith('http://') || imagePath.startsWith('https://')) {
        return imagePath;
      }
      // Sinon, ajouter la base URL
      return getFullPath(imagePath);
    });
    
    // Prcharger chaque image
    const preloadPromises = imageUrls.map(async (url) => {
      try {
        // Vrifier si l'image est dj en cache
        if (cacheManager.has(`${CACHE_KEYS.HISTORY_IMAGES}_${url}`)) {
          console.log(`[OfflineService] Image d'historique dj en cache: ${url}`);
          return url;
        }
        
        // Utiliser fetch pour prcharger l'image et la mettre en cache
        const response = await fetch(url, { 
          cache: 'force-cache',
          headers: {
            'Cache-Control': 'max-age=31536000', // 1 an
          },
          mode: 'no-cors' // Permet le chargement mme en cas de problmes CORS
        });
        
        if (!response.ok && response.type !== 'opaque') {
          throw new Error(`Erreur lors du chargement de l'image d'historique: ${response.status}`);
        }
        
        // Crer un blob  partir de la rponse pour garantir que l'image est compltement tlcharge
        const blob = await response.blob();
        
        // Stocker les mtadonnes de l'image dans le cache
        cacheManager.set(`${CACHE_KEYS.HISTORY_IMAGES}_${url}`, {
          url,
          size: blob.size,
          type: blob.type,
          timestamp: Date.now()
        }, CACHE_EXPIRY);
        
        
        // galement mettre en cache dans le cache du navigateur via le service worker
        if ('serviceWorker' in navigator && navigator.serviceWorker.controller) {
          navigator.serviceWorker.controller.postMessage({
            type: 'CACHE_HISTORY_IMAGE',
            url
          });
        }
        
        return url;
      } catch (error) {
        console.error(`[OfflineService] Erreur lors du prchargement de l'image d'historique ${url}:`, error);
        return null;
      }
    });
    
    const results = await Promise.all(preloadPromises);
    const successfulUrls = results.filter(url => url !== null) as string[];
    
    if (successfulUrls.length < imageUrls.length) {
      console.log(`[OfflineService] ${successfulUrls.length}/${imageUrls.length} images d'historique prcharges`);
    }
    
    return successfulUrls;
  } catch (error) {
    console.error('[OfflineService] Erreur lors du prchargement des images d\'historique:', error);
    return [];
  }
};

/**
 * Prcharge l'image de la carte pour une utilisation hors-ligne
 * @returns Promise<boolean> true si l'image a t prcharge avec succs
 */
export const preloadMapImage = async (): Promise<boolean> => {
  try {
    console.log('[OfflineService] Prchargement de l\'image de la carte');
    
    const mapImageUrl = getFullPath('map-feydeau.png');
    
    // Utiliser fetch pour prcharger l'image et la mettre en cache
    const response = await fetch(mapImageUrl, { 
      cache: 'force-cache',
      // Ajouter des options pour viter les problmes de cache
      headers: {
        'Cache-Control': 'max-age=31536000', // 1 an
        'Pragma': 'no-cache' // Force le rechargement
      },
      mode: 'no-cors' // Permet le chargement mme en cas de problmes CORS
    });
    
    if (!response.ok && response.type !== 'opaque') {
      throw new Error(`Erreur lors du chargement de l'image de la carte: ${response.status}`);
    }
    
    // Crer un blob  partir de la rponse pour garantir que l'image est compltement tlcharge
    const blob = await response.blob();
    
    // Stocker l'URL de l'image et le blob dans le cache
    cacheManager.set(CACHE_KEYS.MAP_IMAGE, {
      url: mapImageUrl,
      size: blob.size,
      type: blob.type,
      timestamp: Date.now()
    }, CACHE_EXPIRY);
    
    
    // galement mettre en cache dans le cache du navigateur via le service worker
    if ('serviceWorker' in navigator && navigator.serviceWorker.controller) {
      navigator.serviceWorker.controller.postMessage({
        type: 'CACHE_MAP_IMAGE',
        url: mapImageUrl
      });
    }
    
    return true;
  } catch (error) {
    console.error('[OfflineService] Erreur lors du prchargement de l\'image de la carte:', error);
    return false;
  }
};

/**
 * Prcharge toutes les donnes ncessaires pour le mode hors-ligne
 * @returns Promise<void>
 */
export const preloadAllOfflineData = async (): Promise<void> => {
  try {
    console.log('[OfflineService] Dmarrage du prchargement des donnes hors-ligne');
    
    // Prcharger l'image de la carte en premier (priorit leve)
    const mapPreloaded = await preloadMapImage();
    console.log(`[OfflineService] Prchargement de la carte: ${mapPreloaded ? 'russi' : 'chou'}`);
    
    // Prcharger les vnements sauvegards
    await preloadSavedEvents();
    
    // Prcharger les lieux
    await preloadLocations();
    
    // Prcharger les images des lieux
    await preloadLocationImages();
    
    // Prcharger les images des historiques des lieux
    await preloadHistoryImages();
    
    // Si le prchargement de la carte a chou, ressayer
    if (!mapPreloaded) {
      console.log('[OfflineService] Nouvelle tentative de prchargement de la carte');
      await preloadMapImage();
    }
    
    console.log('[OfflineService] Prchargement des donnes hors-ligne termin');
  } catch (error) {
    console.error('[OfflineService] Erreur lors du prchargement des donnes hors-ligne:', error);
  }
};

/**
 * Vrifie si les donnes hors-ligne sont disponibles
 * @returns boolean true si les donnes sont disponibles
 */
export const hasOfflineData = (): boolean => {
  return cacheManager.has(CACHE_KEYS.SAVED_EVENTS) && 
         cacheManager.has(CACHE_KEYS.LOCATIONS) &&
         cacheManager.has(CACHE_KEYS.MAP_IMAGE);
};

/**
 * Rcupre l'image de la carte depuis le cache
 * @returns L'URL de l'image de la carte ou null si elle n'est pas en cache
 */
export const getOfflineMapImage = (): string | null => {
  const mapImageData = cacheManager.get<{url: string}>(CACHE_KEYS.MAP_IMAGE);
  return mapImageData ? mapImageData.url : null;
};

/**
 * Vrifie si une image d'historique est disponible dans le cache
 * @param url L'URL de l'image  vrifier
 * @returns boolean true si l'image est disponible dans le cache
 */
export const isHistoryImageCached = (url: string): boolean => {
  return cacheManager.has(`${CACHE_KEYS.HISTORY_IMAGES}_${url}`);
};

/**
 * Prcharge une image d'historique spcifique
 * @param url L'URL de l'image  prcharger
 * @returns Promise<boolean> true si l'image a t prcharge avec succs
 */
export const preloadSingleHistoryImage = async (url: string): Promise<boolean> => {
  try {
    // Vrifier si l'image est dj en cache
    if (isHistoryImageCached(url)) {
      console.log(`[OfflineService] Image dj en cache: ${url}`);
      return true;
    }
    
    console.log(`[OfflineService] Prchargement de l'image: ${url}`);
    
    // Utiliser fetch pour prcharger l'image et la mettre en cache
    const response = await fetch(url, { 
      cache: 'force-cache',
      headers: {
        'Cache-Control': 'max-age=31536000', // 1 an
      },
      mode: 'no-cors' // Permet le chargement mme en cas de problmes CORS
    });
    
    if (!response.ok && response.type !== 'opaque') {
      throw new Error(`Erreur lors du chargement de l'image: ${response.status}`);
    }
    
    // Crer un blob  partir de la rponse pour garantir que l'image est compltement tlcharge
    const blob = await response.blob();
    
    // Stocker les mtadonnes de l'image dans le cache
    cacheManager.set(`${CACHE_KEYS.HISTORY_IMAGES}_${url}`, {
      url,
      size: blob.size,
      type: blob.type,
      timestamp: Date.now()
    }, CACHE_EXPIRY);
    
    console.log(`[OfflineService] Image prcharge: ${url} (${blob.size} octets)`);
    
    // galement mettre en cache dans le cache du navigateur via le service worker
    if ('serviceWorker' in navigator && navigator.serviceWorker.controller) {
      navigator.serviceWorker.controller.postMessage({
        type: 'CACHE_HISTORY_IMAGE',
        url
      });
    }
    
    return true;
  } catch (error) {
    console.error(`[OfflineService] Erreur lors du prchargement de l'image ${url}:`, error);
    return false;
  }
};

