/**
 * Systme de gestion des erreurs et de journalisation pour l'application
 * Ce fichier initialise les gestionnaires d'erreurs globaux et le systme de logs
 */

import { initErrorHandlers, createLogViewer, createLogger } from './logger';

const logger = createLogger('ErrorHandling');

/**
 * Initialise le systme de gestion des erreurs
 *  appeler dans le composant racine de l'application
 */
export function initErrorHandlingSystem() {
  // Initialiser les gestionnaires d'erreurs globaux
  initErrorHandlers();
  
  // Crer le visualiseur de logs (uniquement en dveloppement)
  if (process.env.NODE_ENV === 'development') {
    const logViewer = createLogViewer();
    
    // Exposer le logViewer dans la console pour le dbogage
    (window as any).__logViewer = logViewer;
    
    logger.info('Systme de visualisation des logs initialis (appuyez sur le bouton "Logs" pour voir les logs)');
  }
  
  logger.info('Systme de gestion des erreurs initialis');
}

/**
 * Enregistre une erreur avec des informations contextuelles
 * @param error L'erreur  enregistrer
 * @param context Informations contextuelles supplmentaires
 */
export function logError(error: Error, context?: Record<string, any>) {
  logger.error(`Erreur: ${error.message}`, {
    name: error.name,
    stack: error.stack,
    ...context
  });
}

/**
 * Enregistre une erreur lie  l'API
 * @param endpoint Point d'accs de l'API
 * @param error L'erreur  enregistrer
 */
export function logApiError(endpoint: string, error: any) {
  logger.error(`Erreur API (${endpoint})`, {
    status: error.status,
    statusText: error.statusText,
    message: error.message,
    data: error.data
  });
}

