import { useState, useEffect, useCallback } from 'react';
import { 
  toggleLike as toggleLikeService, 
  getLikeData, 
  getLikeDataFresh,
  getSessionId 
} from '@/services/likesService';
import { LikeData, LikeResponse } from '@/types/likesTypes';

export interface UseLikesReturn {
  liked: boolean;
  total: number;
  loading: boolean;
  error: string | null;
  isToggling: boolean;
  toggleLike: () => Promise<void>;
  refresh: () => Promise<void>;
}

export function useLikes(entryId: string): UseLikesReturn {
  const [liked, setLiked] = useState<boolean>(false);
  const [total, setTotal] = useState<number>(0);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [isToggling, setIsToggling] = useState<boolean>(false);

  const sessionId = getSessionId();

  // Charger les donnes initiales (avec cache)
  const loadLikeData = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      
      const data = await getLikeData(entryId, sessionId);
      setLiked(data.liked);
      setTotal(data.total);
      
      // Log seulement pour les entres non-historiques ou en cas d'erreur
      if (!entryId.startsWith('historical-')) {
        console.log(` Donnes likes charges pour ${entryId}:`, data);
      }
    } catch (err) {
      console.error(' Erreur chargement likes:', err);
      setError(err instanceof Error ? err.message : 'Erreur de chargement');
    } finally {
      setLoading(false);
    }
  }, [entryId, sessionId]);

  // Charger les donnes fraches (pour le polling)
  const loadFreshLikeData = useCallback(async () => {
    try {
      setError(null);
      
      const data = await getLikeDataFresh(entryId, sessionId);
      setLiked(data.liked);
      setTotal(data.total);
      
      // Log seulement pour les entres non-historiques
      if (!entryId.startsWith('historical-')) {
        console.log(` Donnes likes fraches pour ${entryId}:`, data);
      }
    } catch (err) {
      console.error(' Erreur chargement likes fresh:', err);
      setError(err instanceof Error ? err.message : 'Erreur de chargement');
    }
  }, [entryId, sessionId]);

  // Charger les donnes initiales et setup polling simple
  useEffect(() => {
    if (!entryId) return;

    // Log seulement pour les entres non-historiques
    if (!entryId.startsWith('historical-')) {
      console.log(` Chargement initial pour ${entryId}`);
    }
    
    // Charger les donnes initiales
    loadLikeData();
    
    // Setup polling avec donnes fraches (toutes les 5 secondes)
    const interval = setInterval(() => {
      // Log seulement pour les entres non-historiques
      if (!entryId.startsWith('historical-')) {
        console.log(` Polling automatique pour ${entryId}`);
      }
      loadFreshLikeData();
    }, 5000);

    // Cleanup
    return () => {
      // Log seulement pour les entres non-historiques
      if (!entryId.startsWith('historical-')) {
        console.log(` Arrt polling pour ${entryId}`);
      }
      clearInterval(interval);
    };
  }, [entryId, sessionId, loadLikeData, loadFreshLikeData]);

  // Fonction pour toggler le like avec debouncing
  const handleToggleLike = useCallback(async () => {
    // Prvention double-clic : si dj en cours, ignorer
    if (isToggling) {
      console.log(` Toggle like dj en cours pour ${entryId}, ignor`);
      return;
    }

    try {
      setIsToggling(true);
      setError(null);
      console.log(` Toggle like pour ${entryId}...`);
      
      // Optimistic update
      const newLiked = !liked;
      const newTotal = newLiked ? total + 1 : Math.max(0, total - 1);
      
      setLiked(newLiked);
      setTotal(newTotal);
      
      // Appel au service
      const response: LikeResponse = await toggleLikeService(entryId, sessionId);
      
      if (!response.success) {
        // Rollback en cas d'erreur
        setLiked(liked);
        setTotal(total);
        setError(response.error || 'Erreur lors du like');
        console.error(' Erreur toggle like:', response.error);
      } else {
        console.log(` Like toggled avec succs pour ${entryId}`);
      }
      
    } catch (err) {
      // Rollback en cas d'erreur
      setLiked(liked);
      setTotal(total);
      setError(err instanceof Error ? err.message : 'Erreur inconnue');
      console.error(' Erreur toggle like:', err);
    } finally {
      // Dbloquer aprs un dlai minimum pour viter les clics trop rapides
      const timeoutId = setTimeout(() => {
        setIsToggling(false);
      }, 500); // 500ms de dlai minimum entre les clics
      
      // Note: Ce timeout est volontairement non nettoy car il doit se terminer
      // mme si le composant est dmont pour viter les fuites d'tat
    }
  }, [entryId, sessionId, liked, total, isToggling]);

  // Fonction pour rafrachir manuellement
  const refresh = useCallback(async () => {
    await loadLikeData();
  }, [loadLikeData]);

  return {
    liked,
    total,
    loading,
    error,
    isToggling,
    toggleLike: handleToggleLike,
    refresh
  };
}

