import { getAnalytics, setAnalyticsCollectionEnabled, isSupported, setUserId } from "firebase/analytics";
import { getFirebaseApp } from "../services/firebaseConfig";

/**
 * Active le mode debug pour Firebase Analytics
 *  utiliser uniquement en dveloppement
 */
export const initFirebaseDebug = async () => {
  try {
    // Vrifier si Analytics est support
    const supported = await isSupported();
    if (!supported) {
      console.warn("[FirebaseDebug] Analytics n'est pas support dans cet environnement");
      return;
    }

    // Obtenir l'instance Analytics
    const app = getFirebaseApp();
    if (!app) {
      console.warn('Firebase non initialis, impossible d\'utiliser Analytics');
      return;
    }
    const analytics = getAnalytics(app);
    
    // Activer la collecte de donnes
    setAnalyticsCollectionEnabled(analytics, true);
    
    // Activer le mode debug via les mthodes disponibles
    // Note: Firebase Analytics n'a pas de mthode setDebugMode officielle
    // On utilise d'autres approches pour activer le debug
    
    // Dfinir un ID utilisateur pour le debug
    const debugUserId = `debug-user-${Date.now()}`;
    setUserId(analytics, debugUserId);
    
    // Ajouter les paramtres de requte pour activer le mode debug
    const url = new URL(window.location.href);
    url.searchParams.set('firebase_debug', 'true');
    url.searchParams.set('debug_mode', 'true');
    url.searchParams.set('debug_view', 'true');
    window.history.replaceState({}, document.title, url.toString());
    
    // Dfinir la variable globale pour le debug Firebase
    // @ts-ignore - Cette proprit n'est pas dans les types mais est utilise par Firebase
    window.self.FIREBASE_ANALYTICS_DEBUG_MODE = true;
    
    console.log("[FirebaseDebug] Mode debug activ pour Firebase Analytics");
    console.log(`[FirebaseDebug] ID utilisateur de debug: ${debugUserId}`);
    
    // Afficher les instructions pour voir les vnements dans DebugView
    console.info(
      "%c[FirebaseDebug] Pour voir les vnements dans DebugView: %c\n" +
      "1. Ouvrez la console Firebase\n" +
      "2. Allez dans Analytics > DebugView\n" +
      "3. Slectionnez votre appareil de debug",
      "color: #4285F4; font-weight: bold;", "color: #000;"
    );
  } catch (error) {
    console.error("[FirebaseDebug] Erreur lors de l'initialisation du mode debug:", error);
  }
};

/**
 * Force l'envoi d'un vnement de test pour vrifier que DebugView fonctionne
 */
export const sendTestEvent = () => {
  try {
    const app = getFirebaseApp();
    if (!app) {
      console.warn('Firebase non initialis, impossible d\'utiliser Analytics');
      return;
    }
    const analytics = getAnalytics(app);
    if (!analytics) {
      console.warn("[FirebaseDebug] Analytics non disponible pour l'vnement de test");
      return;
    }
    
    // Importer logEvent dynamiquement pour viter les erreurs de compilation
    import('firebase/analytics').then(({ logEvent }) => {
      logEvent(analytics, 'test_debug_event', {
        timestamp: new Date().toISOString(),
        debug: true
      });
      console.log("[FirebaseDebug] vnement de test envoy");
    });
  } catch (error) {
    console.error("[FirebaseDebug] Erreur lors de l'envoi de l'vnement de test:", error);
  }
};

