import { createLogger } from "@/utils/logger";
import { GeoPosition, isPositionWithinFeydeau, calculateDistanceToCenter } from "@/utils/locationUtils";
import { FEYDEAU_CENTER } from "@/data/gpsCoordinates";
import { toastService } from "@/services/toastService";

// Crer un logger pour le service avec filtrage des logs indsirables
const originalLogger = createLogger('locationService');

// Wrapper pour filtrer les logs indsirables
const logger = {
  info: (message: string, data?: any) => {
    // Filtrer les logs lis  la position et au marqueur utilisateur
    if (message.includes('Position utilisateur') || 
        message.includes('Rendu du marqueur') || 
        message.includes('mise  jour')) {
      // Prserver les logs d'loignement
      if (message.includes('Toast d\'loignement')) {
        return originalLogger.info(message, data);
      }
      // Ignorer les autres logs indsirables
      return;
    }
    return originalLogger.info(message, data);
  },
  warn: originalLogger.warn,
  error: originalLogger.error,
  debug: (message: string, data?: any) => {
    // Filtrer les logs de debug lis  la position et au marqueur
    if (message.includes('Position utilisateur') || 
        message.includes('Rendu du marqueur') || 
        message.includes('mise  jour')) {
      return;
    }
    return originalLogger.debug(message, data);
  }
};

// Constantes pour le service de localisation
const LOCATION_UPDATE_INTERVAL = 5000; // 5 secondes
const LOCATION_TIMEOUT = 10000; // 10 secondes
const LOCATION_MAX_AGE = 30000; // 30 secondes
const LOCATION_CONSENT_KEY = 'locationConsent';
const LOCATION_ACTIVATION_TIMESTAMP_KEY = 'locationActivationTimestamp';
const RECENT_ACTIVATION_THRESHOLD = 5000; // 5 secondes

// Options pour la golocalisation
const geoOptions: PositionOptions = {
  enableHighAccuracy: true,
  timeout: LOCATION_TIMEOUT,
  maximumAge: LOCATION_MAX_AGE
};

// Type pour les callbacks de mise  jour de position
export type LocationUpdateCallback = (position: GeoPosition) => void;
export type MapCoordinatesUpdateCallback = (x: number, y: number, position?: GeoPosition) => void;
export type PermissionChangeCallback = (denied: boolean) => void;

// Interface pour le service de localisation
export interface LocationServiceInterface {
  startTracking: (
    onLocationUpdate: MapCoordinatesUpdateCallback,
    onPermissionChange?: PermissionChangeCallback
  ) => void;
  stopTracking: () => void;
  isLocationActivated: () => boolean;
  activateLocation: () => void;
  deactivateLocation: () => void;
}

/**
 * Service pour grer le suivi de la localisation de l'utilisateur
 */
class LocationService implements LocationServiceInterface {
  private watchId: number | null = null;
  private lastPosition: GeoPosition | null = null;
  private onLocationUpdateCallback: MapCoordinatesUpdateCallback | null = null;
  private onPermissionChangeCallback: PermissionChangeCallback | null = null;
  private permissionDenied: boolean = false;
  private isLocalDevelopment: boolean = false;
  private simulationInterval: number | null = null;

  constructor() {
    // Dtecter si nous sommes en environnement de dveloppement local
    // Pour les tests sur le terrain, on force cette valeur  true pour tester le mode simulation
    this.isLocalDevelopment = false;
    
    // Vrifier si la golocalisation est disponible
    if (!navigator.geolocation && !this.isLocalDevelopment) {
      logger.warn('La golocalisation n\'est pas prise en charge par ce navigateur');
    }
    
    // Ajouter un gestionnaire d'vnement pour dtecter quand l'application revient au premier plan
    document.addEventListener('visibilitychange', this.handleVisibilityChange.bind(this));
  }

  /**
   * Gre les changements de visibilit de la page
   * Rinitialise le suivi de localisation quand l'application revient au premier plan
   */
  private handleVisibilityChange(): void {
    // Si la page redevient visible et que le suivi est actif
    if (document.visibilityState === 'visible' && this.watchId !== null && this.isLocationActivated()) {
      logger.info('Application revenue au premier plan, rinitialisation du suivi de localisation');
      
      // Stocker temporairement les callbacks
      const updateCallback = this.onLocationUpdateCallback;
      const permissionCallback = this.onPermissionChangeCallback;
      
      // Arrter le suivi actuel
      this.stopTracking();
      
      // Redmarrer le suivi avec les mmes callbacks
      if (updateCallback) {
        setTimeout(() => {
          this.startTracking(updateCallback, permissionCallback || undefined);
        }, 500); // Petit dlai pour s'assurer que l'arrt est bien effectu
      }
    }
  }
  
  /**
   * Dmarre le suivi de la localisation
   * @param onLocationUpdate Callback pour les mises  jour de position sur la carte
   * @param onPermissionChange Callback pour les changements d'tat de permission
   */
  public startTracking(
    onLocationUpdate: MapCoordinatesUpdateCallback,
    onPermissionChange?: PermissionChangeCallback
  ): void {
    this.onLocationUpdateCallback = onLocationUpdate;
    this.onPermissionChangeCallback = onPermissionChange || null;
    
    // Rinitialiser l'tat de permission refuse au dmarrage
    this.permissionDenied = false;
    
    // Notifier que la permission n'est pas refuse
    this.notifyPermissionChange(false);
    
    // Si la localisation n'est pas active, ne pas dmarrer le suivi
    if (!this.isLocationActivated()) {
      logger.info('La localisation n\'est pas active, le suivi ne dmarre pas');
      return;
    }
    
    // En mode dveloppement local, simuler la position
    if (this.isLocalDevelopment) {
      this.startPositionSimulation();
      return;
    }
    
    // Vrifier si la golocalisation est disponible
    if (!navigator.geolocation) {
      logger.warn('La golocalisation n\'est pas prise en charge par ce navigateur');
      toastService.error({
        title: "Localisation non disponible",
        description: "Votre navigateur ne prend pas en charge la golocalisation.",
        source: "LocationService.startTracking",
        context: {
          browserSupport: false
        }
      });
      return;
    }
    
    // Forcer une demande d'autorisation de golocalisation immdiatement
    navigator.geolocation.getCurrentPosition(
      this.handlePositionSuccess.bind(this),
      this.handlePositionError.bind(this),
      geoOptions
    );
    
    // Dmarrer le suivi de la position
    const id = navigator.geolocation.watchPosition(
      this.handlePositionSuccess.bind(this),
      this.handlePositionError.bind(this),
      geoOptions
    );
    
    this.watchId = id;
    // Log de dmarrage supprim pour rduire le bruit dans la console
  }

  /**
   * Arrte le suivi de la localisation
   */
  public stopTracking(): void {
    // Arrter la simulation si elle est active
    if (this.simulationInterval !== null) {
      window.clearInterval(this.simulationInterval);
      this.simulationInterval = null;
      // Log d'arrt de simulation supprim pour rduire le bruit dans la console
    }
    
    // Arrter le suivi si actif
    if (this.watchId !== null) {
      navigator.geolocation.clearWatch(this.watchId);
      this.watchId = null;
      // Log d'arrt de suivi supprim pour rduire le bruit dans la console
    }
    
    // Rinitialiser les callbacks
    this.onLocationUpdateCallback = null;
    this.onPermissionChangeCallback = null;
  }

  /**
   * Vrifie si la localisation est active
   * @returns true si la localisation est active, false sinon
   */
  public isLocationActivated(): boolean {
    return localStorage.getItem(LOCATION_CONSENT_KEY) === 'granted';
  }

  /**
   * Active la localisation
   */
  public activateLocation(): void {
    localStorage.setItem(LOCATION_CONSENT_KEY, 'granted');
    localStorage.setItem(LOCATION_ACTIVATION_TIMESTAMP_KEY, Date.now().toString());
    // Suppression du log pour rduire le bruit dans la console
    
    toastService.success({
      title: "Localisation active",
      description: "Vous pouvez maintenant voir votre position sur la carte.",
      duration: 3000,
      source: "LocationService.activateLocation",
      context: {
        timestamp: Date.now()
      }
    });
  }

  /**
   * Dsactive la localisation
   */
  public deactivateLocation(): void {
    localStorage.removeItem(LOCATION_CONSENT_KEY);
    localStorage.removeItem(LOCATION_ACTIVATION_TIMESTAMP_KEY);
    // Suppression du log pour rduire le bruit dans la console
    
    toastService.show({
      title: "Localisation dsactive",
      description: "Votre position ne sera plus suivie.",
      duration: 3000,
      source: "LocationService.deactivateLocation",
      context: {
        timestamp: Date.now()
      }
    });
    
    // Arrter le suivi
    this.stopTracking();
  }

  /**
   * Vrifie si la localisation a t active rcemment
   * @returns true si la localisation a t active il y a moins de RECENT_ACTIVATION_THRESHOLD ms
   */
  private isRecentlyActivated(): boolean {
    const activationTimestamp = localStorage.getItem(LOCATION_ACTIVATION_TIMESTAMP_KEY);
    if (!activationTimestamp) return false;
    
    const timestamp = parseInt(activationTimestamp, 10);
    const now = Date.now();
    return now - timestamp < RECENT_ACTIVATION_THRESHOLD;
  }

  /**
   * Notifie le changement d'tat de permission
   * @param denied true si la permission est refuse, false sinon
   */
  private notifyPermissionChange(denied: boolean): void {
    if (this.permissionDenied !== denied) {
      this.permissionDenied = denied;
      
      if (this.onPermissionChangeCallback) {
        this.onPermissionChangeCallback(denied);
      }
    }
  }

  /**
   * Gre le succs de la golocalisation
   * @param pos Position reue du navigateur
   */
  private handlePositionSuccess(pos: GeolocationPosition): void {
    // Extraire les donnes de position
    const position: GeoPosition = {
      latitude: pos.coords.latitude,
      longitude: pos.coords.longitude,
      accuracy: pos.coords.accuracy
    };
    
    // Enregistrer la position
    this.lastPosition = position;
    
    // Dsactivation complte des logs de position pour viter de surcharger la console
    // Les logs ne seront gnrs que pour les vnements importants (dmarrage/arrt du suivi, erreurs)
    
    // Convertir les coordonnes GPS en coordonnes de carte
    // Cette conversion est gre par le composant qui utilise ce service
    
    // Vrifier si l'utilisateur est dans l'le Feydeau
    const isWithin = isPositionWithinFeydeau(position);
    
    // Calculer la distance au centre
    const distanceToCenter = calculateDistanceToCenter(position.latitude, position.longitude);
    const isFarFromCenter = distanceToCenter > 300; // Plus de 300 mtres
    
    // Dterminer s'il faut afficher une notification
    const isFirstPosition = this.lastPosition === position;
    const isRecentlyActivated = this.isRecentlyActivated();
    
    // Afficher une notification si l'utilisateur est loin de l'le
    if (!isWithin && !isFirstPosition && !isRecentlyActivated && isFarFromCenter) {
      // Cette notification est maintenant gre par le composant qui utilise ce service
    }
    
    // Notifier la mise  jour de position
    if (this.onLocationUpdateCallback) {
      // Le callback doit grer la conversion GPS -> carte
      this.onLocationUpdateCallback(0, 0, position);
    }
  }

  /**
   * Gre les erreurs de golocalisation
   * @param err Erreur de golocalisation
   */
  private handlePositionError(err: GeolocationPositionError): void {
    // Ne pas logger les erreurs de timeout, elles sont normales
    if (err.code !== err.TIMEOUT) {
      logger.error('Erreur de golocalisation', {
        code: err.code,
        message: err.message
      });
    }
    
    // Grer les diffrents types d'erreurs
    switch (err.code) {
      case err.PERMISSION_DENIED:
        this.notifyPermissionChange(true);
        break;
        
      case err.POSITION_UNAVAILABLE:
        // Notification gre par le composant
        break;
        
      case err.TIMEOUT:
        // Ignorer les timeouts, ils sont normaux
        break;
    }
  }

  /**
   * Dmarre la simulation de position pour le dveloppement local
   */
  private startPositionSimulation(): void {
    // Suppression du log pour rduire le bruit dans la console
    
    // Simuler une position initiale au centre de l'le Feydeau (utiliser les coordonnes du point de rfrence)
    const initialPosition: GeoPosition = {
      latitude: 47.212600, // Centre de rfrence de la carte
      longitude: -1.555900, // Centre de rfrence de la carte
      accuracy: 10 // 10 mtres de prcision
    };
    
    // Notifier immdiatement avec la position initiale
    this.handlePositionSuccess({
      coords: {
        latitude: initialPosition.latitude,
        longitude: initialPosition.longitude,
        accuracy: initialPosition.accuracy,
        altitude: null,
        altitudeAccuracy: null,
        heading: null,
        speed: null
      },
      timestamp: Date.now()
    } as GeolocationPosition);
    
    // Mettre  jour la position simule priodiquement
    this.simulationInterval = window.setInterval(() => {
      // Gnrer une lgre variation alatoire pour simuler un mouvement
      const latOffset = (Math.random() - 0.5) * 0.0002; // Environ 10 mtres
      const lngOffset = (Math.random() - 0.5) * 0.0002; // Environ 10 mtres
      
      const simulatedPosition: GeoPosition = {
        latitude: 47.212600 + latOffset, // Centre de rfrence de la carte + offset
        longitude: -1.555900 + lngOffset, // Centre de rfrence de la carte + offset
        accuracy: 5 + Math.random() * 10 // Entre 5 et 15 mtres de prcision
      };
      
      // Notifier avec la position simule
      this.handlePositionSuccess({
        coords: {
          latitude: simulatedPosition.latitude,
          longitude: simulatedPosition.longitude,
          accuracy: simulatedPosition.accuracy,
          altitude: null,
          altitudeAccuracy: null,
          heading: null,
          speed: null
        },
        timestamp: Date.now()
      } as GeolocationPosition);
    }, LOCATION_UPDATE_INTERVAL);
  }
}

// Exporter une instance unique du service
export const locationService = new LocationService();

