/**
 * Utilitaire pour grer les chemins d'images en tenant compte du prfixe de base
 * pour GitHub Pages en production
 */

// Utiliser la base URL dynamique dfinie dans index.html
export const getBasePath = (): string => {
  // Rcuprer la base URL depuis la configuration globale
  if (typeof window !== 'undefined' && window.APP_CONFIG && window.APP_CONFIG.BASE_URL) {
    // Enlever le slash final s'il existe pour la cohrence
    const baseUrl = window.APP_CONFIG.BASE_URL.endsWith('/') 
      ? window.APP_CONFIG.BASE_URL.slice(0, -1) 
      : window.APP_CONFIG.BASE_URL;
    return baseUrl;
  }
  
  // Fallback pour le dveloppement local ou si la configuration n'est pas disponible
  return import.meta.env.PROD ? '/1Hall1Artiste' : '';
};

/**
 * Retourne le chemin complet d'une image en ajoutant le prfixe de base si ncessaire
 * @param path Chemin relatif de l'image (commenant par /)
 * @returns Chemin complet de l'image avec le prfixe de base si en production
 */
export function getImagePath(path: string): string {
  // S'assurer que le chemin commence par un slash
  const normalizedPath = path.startsWith('/') ? path : `/${path}`;
  
  // Utiliser la fonction getBasePath pour obtenir le prfixe dynamique
  const basePath = getBasePath();
  
  // PROTECTION CONTRE LA DOUBLE PRFIXATION
  // Si le chemin contient dj le basePath, ne pas l'ajouter  nouveau
  if (basePath && normalizedPath.startsWith(basePath)) {
    console.log(' [getImagePath] Double prefixing detected! Using path as-is:', {
      input: path,
      normalizedPath,
      basePath,
      result: normalizedPath
    });
    return normalizedPath;
  }
  
  // Encoder les espaces et caractres spciaux pour les URL
  // Mais prserver les slashes et autres caractres d'URL valides
  const encodedPath = normalizedPath.split('/').map(segment => 
    segment ? encodeURIComponent(segment) : ''
  ).join('/');
  
  const fullPath = `${basePath}${encodedPath}`;
  
  // Log dtaill pour debug
  console.log(' [getImagePath] Processing:', {
    input: path,
    normalizedPath,
    encodedPath,
    basePath,
    fullPath,
    appConfig: typeof window !== 'undefined' ? window.APP_CONFIG : 'undefined',
    isProd: import.meta.env.PROD
  });
  
  return fullPath;
}

