import React from 'react';
import { useNavigate } from "react-router-dom";
import { TreasureButton } from "./TreasureButton";

interface BackButtonProps {
  /** URL de destination. Par dfaut : "/" */
  to?: string;
  /** Fonction personnalise de navigation. Si fournie, ignore 'to' */
  onClick?: () => void;
  /** Texte du bouton. Par dfaut : "Retour" */
  children?: React.ReactNode;
  /** Classes CSS supplmentaires */
  className?: string;
  /** Variant du bouton. Par dfaut : "primary" */
  variant?: 'primary' | 'secondary' | 'outline';
  /** Taille du bouton. Par dfaut : "md" */
  size?: 'sm' | 'md' | 'lg';
}

/**
 * Bouton "Retour" standardis avec le style "carte au trsor"
 * Utilis dans toutes les pages pour une cohrence visuelle
 * Maintenant bas sur TreasureButton pour une cohrence maximale
 */
export const BackButton: React.FC<BackButtonProps> = ({
  to = "/",
  onClick,
  children = "Retour",
  className = "",
  variant = "primary",
  size = "md"
}) => {
  const navigate = useNavigate();

  const handleClick = () => {
    if (onClick) {
      onClick();
    } else {
      navigate(to);
    }
  };

  return (
    <TreasureButton
      variant={variant}
      size={size}
      onClick={handleClick}
      className={className}
    >
      {children}
    </TreasureButton>
  );
};

export default BackButton;

