import { useState, useEffect } from 'react';

interface WindowSize {
  width: number;
  height: number;
}

/**
 * Hook qui retourne les dimensions actuelles de la fentre
 * et se met  jour automatiquement lors du redimensionnement
 */
export const useWindowSize = (): WindowSize => {
  const [windowSize, setWindowSize] = useState<WindowSize>({
    width: typeof window !== 'undefined' ? window.innerWidth : 0,
    height: typeof window !== 'undefined' ? window.innerHeight : 0,
  });

  useEffect(() => {
    // Gestionnaire pour mettre  jour les dimensions lors du redimensionnement
    const handleResize = () => {
      setWindowSize({
        width: window.innerWidth,
        height: window.innerHeight,
      });
    };

    // Ajouter l'couteur d'vnement
    window.addEventListener('resize', handleResize);
    
    // Appeler le gestionnaire immdiatement pour dfinir les dimensions initiales
    handleResize();
    
    // Nettoyer l'couteur d'vnement lors du dmontage
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return windowSize;
};

