import { createRoot } from 'react-dom/client'
import App from './App.tsx'
import './index.css'
import './styles/animations.css'
import './styles/responsive.css'
import { initErrorHandlingSystem } from './utils/errorHandling'
import { initFirebaseDebug, sendTestEvent } from './debug/firebaseDebug'
import { initGtagDebug, sendGtagTestEvent } from './debug/gtag-debug'
import './debug/analytics-realtime-test'
import { directAnalytics } from './services/directAnalytics'

// La gestion des logs est maintenant gre par le systme de logging centralis
// dans utils/logger.ts

// Initialiser le systme de gestion des erreurs
initErrorHandlingSystem();

// Initialiser le mode debug Firebase en dveloppement
if (import.meta.env.DEV) {
  // Mthode 1: Firebase Debug
  initFirebaseDebug();
  
  // Mthode 2: Gtag Debug (alternative)
  initGtagDebug();
  
  // Envoyer des vnements de test aprs un court dlai
  setTimeout(() => {
    // Tester les deux mthodes
    sendTestEvent();
    sendGtagTestEvent();
    
    // Tester le nouveau service direct
    directAnalytics.testRealTime();
    
    console.log("[Main] vnements de test envoys depuis main.tsx");
    console.log("[Main] Fonctions de test temps rel disponibles dans la console");
    console.log("[Main] Service Direct Analytics initialis");
  }, 2000);
}

createRoot(document.getElementById("root")!).render(<App />);

