// Locations data for the map
export type Location = {
  id: string;
  name: string;
  x: number;
  y: number;
  description: string;
  history?: string; // Historique complet du lieu
  historyRef?: string; // Rfrence  l'ID d'un autre lieu pour partager son historique
  image?: string; // URL de l'image du lieu
  audio?: string; // URL du fichier audio pour l'histoire du lieu
  visited?: boolean;
  hasProgram?: boolean; // Indique si le lieu a une programmation d'vnements
  gps?: {
    latitude: number;
    longitude: number;
  }; // Coordonnes GPS prcises
};

// Fonction utilitaire pour obtenir le nom d'un lieu  partir de son ID
export function getLocationNameById(locationId: string): string {
  const location = locations.find(loc => loc.id === locationId);
  return location ? location.name : '';
}

// Fonction utilitaire pour obtenir les coordonnes GPS d'un lieu  partir de son ID
export function getLocationGPSById(locationId: string): { latitude: number; longitude: number } | null {
  const location = locations.find(loc => loc.id === locationId);
  return location && location.gps ? location.gps : null;
}

// Fonction utilitaire pour rcuprer tous les lieux
export function getLocations(): Location[] {
  return locations;
}

