    name: "10 quai Turenne / 13 rue Kervgan",
    x: 260,
    y: 224,
    description: "Immeuble de ngociant (1755-56). Construit par Joseph Raimbaud, marchand de bois. Innovation architecturale: premier immeuble de Feydeau avec des appartements  couloir plutt qu'en enfilade. 3 tages et combles sans entresol. Inscrit aux Monuments Historiques depuis 1984.",
    image: "/10 quai Turenne.jpg",
    audio: "/audio/10-quai-Turenne.mp3",
    history: "Un immeuble de ngociant\n\nJacques Goubert, ingnieur, est un des actionnaires d'origine du lotissement. Il est l'auteur des plans du parcellaire. Il propose galement dans son projet un modle d'lvation qui ne sera pas suivi par les constructeurs.\nIl revend sa parcelle  Joseph Raimbaud, marchand de bois en lien avec le milieu de la construction navale. Celui-ci fait construire l'immeuble en 1755-56.\n\n3 tages et combles sans entresol\n\nLes immeubles du lotissement Feydeau sont tantt entresol, tantt non. La prsence de l'entresol rvle l'importance accorde  l'activit commerciale : celui-ci permet d'installer des rserves et des bureaux. Dans le cas prsent, les constructeurs ont souhait privilgier le logement. Aussi le rez-de-chausse est-il occup par des appartements.\n\nUne innovation : des appartements avec un couloir\n\nJusqu'au dbut du XVIIIe sicle, on ne connat qu'une seule manire d'agencer les pices d'un appartement : l'enfilade. Les diffrentes salles se commandent les unes les autres. Au fil du sicle, le dsir d'intimit grandissant, on voit apparatre le couloir, jug plus pratique. Cet immeuble est le seul dans Feydeau  prsenter cette particularit.\n\nUne dcoration soigne\n\nContrairement  d'autres immeubles du lotissement, les faades sur rue et quai sont identiques. Toutes les ouvertures comportent un mascaron, avec des visages africains. Les balcons sont construits sur des consoles au 1er tage et sur trompe au 2me. Les angles de la cour ainsi que les demi-tourelles de latrines sont  pans coups. L'immeuble est Inscrit sur l'inventaire supplmentaire des monuments historiques depuis 1984.",
