    audio: "/audio/11-allee-duguay-trouin.mp3",
    history: "Un immeuble de rapport\n\nBertrand Doudet, ngociant, acquiert en 1747 une des parcelles du lotissement auprs de la veuve Beaulieu-Belloteau, dont le mari tait un des actionnaires d'origine. En 1752, la 'maison' est termine : il en habite un des appartements. Les autres sont probablement lous. \n\nDes appartements donnant sur des galeries\n\nDe tous les immeubles de l'le Feydeau, c'est dans celui-ci que ce systme constructif est le plus dvelopp : l'unique escalier ouvre sur trois faces de galeries. Chaque tage comprend  l'origine deux appartements : l'un ct Kervgan, l'autre ct Duguay-Trouin, soit un peu plus de 200 m2 par appartement. On trouve donc deux portes d'entre sur chaque palier de l'escalier, qui donnent sur la coursire de chaque appartement. Sur celle-ci ouvrent les porte-fentres de chaque pice. On trouve galement, juste aprs la porte d'entre, des toilettes. On habite en fonction de sa condition \n\nOn peut supposer que Bertrand Doudet s'tait rserv un des deux appartements du 1er tage. Il faut imaginer qu' l'poque, habiter haut, c'est multiplier les contraintes : monter les seaux d'eau, le bois Par ailleurs, le 1er tage est toujours celui qui offre la plus grande hauteur sous plafond, les plus belles chemines, et les lments de dcoration les plus remarquables (boiseries, parquets  la franaise, fontaines  eau dans des niches...). Certains de ces lments sont encore prsents dans l'immeuble.\n\nNi class, ni inscrit\n\nMme s'il s'agit d'un immeuble remarquable, cette maison ne comporte pas d'lments de dcoration qui justifient une demande de protection. La faade ne prsente ni mascarons, ni balcons sur trompe ou sur consoles. Ce type d'immeuble se retrouve frquemment dans le centre ancien. Ce qui fait la typicit de celui-ci, comme de tous les immeubles de Feydeau, est la largeur des faades, qui a permis de crer dans chacun des appartements une enfilade de trois grandes pices.\n\nLes modes passent\n\nA la fin du XVIIIme sicle, Feydeau n'est plus  la mode. Les Nantais prfrent habiter les nouveaux quartiers plus en hauteur : les cours prs de la Cathdrale et surtout le quartier Graslin. Les propritaires changent et certains des appartements sont diviss. Les habitants des immeubles sont souvent de condition plus modeste.\n\nLes vicissitudes de la seconde guerre mondiale\n\nL'immeuble a souffert durant les bombardements. En 1945, sa faade ct Duguay-Trouin est pour partie dtruite. La SNCF, locataire de l'immeuble pour y loger son personnel, fait dmolir et reconstruire cette faade  l'identique. Seules les baies du rez-de-chausse sont agrandies, pour faciliter l'activit commerciale. Pour ce chantier, on rutilise les parpaings de granite du rez-de-chausse, et on remplace le tuffeau des tages par une pierre plus dure. L'immeuble a fait l'objet d'une restauration gnrale durant les annes 1990. La faade ct Duguay-Trouin en cours de reconstruction au printemps 1948.",
